    display: flex;

    .input-div {
        width: 70%;
    }

    .add-btns {
        flex: 1;
    }
}

.selected-label {
    font-size: 12px;
    color: green;
    margin-left: 10px;
}

.item {
    margin-bottom: 16px;
}

.noData {
    line-height: 48px;
    text-align: center;
    color: #606266;
}

.line {
    height: 20px;
    border-bottom: 1px solid #ddd;
}

.marginTop {
    margin-top: 16px;
}

/deep/ .el-tag {
    margin-right: 20px;
    margin-bottom: 16px;
}

/deep/ .el-form-item {
    margin-bottom: 0;
}

.item /deep/ .el-table {
    user-select: none;
}

.item /deep/ .el-table tr {
    cursor: pointer;
}

.link {
    cursor: pointer;
    color: #0628f7;
    text-decoration: underline;
}

.cancelBtn {
    margin-left: 24px;
    font-size: 0.12rem;
    border-radius: 0.03rem;
    padding: 0.09rem 0.15rem;
}
