
























































.page {
    display: flex;
    justify-content: flex-end;
}

.cus-btn {
    color: #2E4AF7;
    font-size: 14px;
    padding-left: 25px;
    cursor: pointer;
    line-height: 22px;
    position: relative;

    &:first-child {
        padding-left: 0;
    }

}

.line:after {
    content: "";
    position: absolute;
    display: block;
    right: -15px;
    top: 6px;
    bottom: 0;
    height: 12px;
    width: 1px;
    background: #E3E5EC;
}

.table-btns {
    cursor: pointer;
    display: flex;
    padding: 0 10px;
    justify-content: space-between;
    align-items: center;

    .edit-btn:hover {
        color: #2E4AF7
    }

    .delete-btn:hover {
        color: #E6A23C
    }

    .center {
        width: 100%;
    }

    .btn {
        color: #2E4AF7;
        text-decoration: underline
    }

}


/deep/ .active-row {
    background-color: #F5F7FA;
}

.avatar {
    display: flex;
    align-items: center;

    img {
        width: 25px;
        height: 25px;
    }
}
